import React, {
  useState,
  useEffect,
  useCallback,
  useMemo,
  useRef,
} from "react";
import "react-datepicker/dist/react-datepicker.css";
import "./ProductsForm.css";
import { useTelegram, useApplyTelegramTheme } from "../../hooks/useTelegram";
import apiProducts from "../../services/apiProducts";
import DatePicker from "react-datepicker";
import Button from "../ui/Button";
import Table from "../ui/Table";

const ProductsForm = () => {
  const {
    WebApp,
    MainButton,
    user,
    sendDataToServer,
    isDevMode,
    CHAT_ID,
    BOT_USERNAME,
    showNotification,
    isTelegramEnvironment,
  } = useTelegram();
  useApplyTelegramTheme();
  let lastRefreshTime = 0;
  let isRefreshDisabled = false;
  const REFRESH_COOLDOWN = 3000; //  
  const tg_user_id = user?.id || (isDevMode() === true ? 1264828537 : null);

  const [isLoading, setIsLoading] = useState(false);
  const [selectedDate, setSelectedDate] = useState(new Date());
  const [selectedReportType, setSelectedReportType] = useState(0);
  const [productsData, setProductsData] = useState([]);
  const [totalCount, setTotalCount] = useState(0);
  const [totalSum, setTotalSum] = useState(0);

  const reportTypes = [
    { value: 0, label: "  " },
    { value: 1, label: "  " },
    { value: 2, label: "  " },
  ];

  //    
  const tableColumns = useMemo(() => {
    switch (selectedReportType) {
      case 0: //   
        return [
          { key: "product_id", title: "ID", width: "5%" },
          { key: "product", title: "", width: "25%" },
          {
            key: "cost",
            title: "",
            width: "15%",
            align: "right",
            render: (value) =>
              `${value?.toString().replace(/\B(?=(\d{3})+(?!\d))/g, " ")} `,
          },
          {
            key: "type_sell_name",
            title: " ",
            width: "10%",
          },
          {
            key: "payer",
            title: "",
            width: "20%",
          },
          {
            key: "datetime_ins",
            title: "",
            width: "20%",
          },
        ];
      case 1: //   
      case 2: //   
        return [
          { key: "date", title: "", width: "10%", align: "center" },
          { key: "total_items", title: "-", width: "10%", align: "right" },
          {
            key: "total_cost",
            title: "",
            width: "40%",
            align: "right",
            render: (value) =>
              `${value?.toString().replace(/\B(?=(\d{3})+(?!\d))/g, " ")} `,
          },
        ];
      default:
        return [];
    }
  }, [selectedReportType]);

  const handleRefresh = useCallback(async () => {
    if (!tg_user_id || isRefreshDisabled) {
      return;
    }
    const now = Date.now();
    if (now - lastRefreshTime < REFRESH_COOLDOWN) {
      console.log("  -   .");
      return;
    }

    lastRefreshTime = now;
    isRefreshDisabled = true;
    setIsLoading(true);
    console.log(
      `${new Date(now).toISOString().replace("T", " ").replace("Z", "")}`,
      " ..."
    );

    try {
      let resData; // 
      let resTotal; // 
      const dateStr = selectedDate.toISOString().split("T")[0];

      switch (selectedReportType) {
        case 0:
          resData = await apiProducts.getDay(tg_user_id, dateStr);
          resTotal = await apiProducts.getDaySum(tg_user_id, dateStr);
          break;
        case 1:
          resData = await apiProducts.getMonth(tg_user_id, dateStr);
          resTotal = await apiProducts.getMonthSum(tg_user_id, dateStr);
          break;
        case 2:
          resData = await apiProducts.getYear(tg_user_id, dateStr);
          resTotal = await apiProducts.getYearSum(tg_user_id, dateStr);
          break;
        default:
          return;
      }

      //        
      const dataArray = Array.isArray(resData)
        ? resData
        : resData?.data && Array.isArray(resData.data)
        ? resData.data
        : resData?.items && Array.isArray(resData.items)
        ? resData.items
        : [];

      const totalData = resTotal?.data?.[0] || resTotal || {};

      setProductsData(dataArray);
      setTotalCount(totalData.total_items || 0);
      setTotalSum(totalData.total_cost || 0);
    } catch (error) {
      console.error("   :", error);
    } finally {
      setIsLoading(false);
      setTimeout(() => {
        isRefreshDisabled = false;
      }, REFRESH_COOLDOWN);
    }
  }, [selectedDate, selectedReportType, tg_user_id]);

  //    CSV 
  const generateCSVContent = useCallback(() => {
    if (
      !productsData ||
      !Array.isArray(productsData) ||
      productsData.length === 0
    ) {
      return null;
    }

    //   CSV
    const csvSeparator = ";";
    const headers = tableColumns.map((col) => col.title).join(csvSeparator);

    //   
    const rows = productsData.map((item) => {
      return tableColumns
        .map((col) => {
          let value = item[col.key];

          //     
          if (col.render) {
            value = col.render(value);
          }

          //      
          if (
            typeof value === "string" &&
            (value.includes(csvSeparator) || value.includes('"'))
          ) {
            value = `"${value.replace(/"/g, '""')}"`;
          }

          return value || "";
        })
        .join(csvSeparator);
    });

    //    
    return [headers, ...rows].join("\n");
  }, [productsData, tableColumns]);

  const handleCopyTable = useCallback(async () => {
    const csvContent = generateCSVContent();
    if (!csvContent) {
      console.log("   ");
      showNotification("   ", "info");
      return;
    }

    try {
      await navigator.clipboard.writeText(csvContent); //    
      console.log("    ");
      showNotification("    ", "success"); //   
    } catch (error) {
      console.error("  :", error);
      showNotification("   ", "error");
    }
  }, [generateCSVContent, showNotification]);

  const handleCopyTotal = useCallback(async () => {
    let csvContent = generateCSVContent();
    if (!csvContent) {
      console.log("   ");
      showNotification("   ", "info");
      return;
    }
    csvContent += `\n-;${totalCount};;${totalSum}`;

    try {
      await navigator.clipboard.writeText(csvContent); //    
      console.log("    ");
      showNotification("    ", "success"); //   
    } catch (error) {
      console.error("  :", error);
      showNotification("   ", "error");
    }
  }, [generateCSVContent, showNotification]);

  const handleDownloadCSV = useCallback(async () => {
    const csvContent = generateCSVContent();
    if (!csvContent) {
      console.log("   ");
      showNotification("   ", "info");
      return;
    }

    try {
      //   
      const fullContent =
        csvContent + `\n-;${totalCount};;${totalSum}`;

      //  Blob  BOM      Excel
      const BOM = "\uFEFF";
      const blob = new Blob([BOM + fullContent], {
        type: "text/csv;charset=utf-8;",
      });

      //    
      const url = window.URL.createObjectURL(blob);
      const link = document.createElement("a");
      link.href = url;

      //     
      const dateStr = selectedDate.toISOString().split("T")[0];
      const reportTypeNames = ["day", "month", "year"];
      const fileName = `products_${reportTypeNames[selectedReportType]}_${dateStr}.csv`;

      link.download = fileName;
      document.body.appendChild(link);
      link.click();
      document.body.removeChild(link);
      window.URL.revokeObjectURL(url);

      console.log(" CSV ");
      showNotification(" CSV ", "success");
    } catch (error) {
      console.error("   :", error);
      showNotification("   ", "error");
    }
  }, [
    generateCSVContent,
    totalCount,
    totalSum,
    selectedDate,
    selectedReportType,
    showNotification,
  ]);

  const handleDropdownItemClick = useCallback(
    (item) => {
      switch (item.action) {
        case "copyTable":
          handleCopyTable();
          break;
        case "copyTotal":
          handleCopyTotal();
          break;
        case "downloadCSV":
          handleDownloadCSV();
          break;
        default:
          break;
      }
    },
    [handleCopyTable, handleCopyTotal, handleDownloadCSV]
  );

  const exportDropdownItems = [
    { label: "  - ", action: "copyTable" },
    { label: "  -  + ", action: "copyTotal" },
    { label: " CSV", action: "downloadCSV" },
  ];

  const handleRowClick = useCallback((row, index) => {
    console.log("  :", row, index);
    //         
  }, []);

  //       
  useEffect(() => {
    if (tg_user_id) {
      handleRefresh();
    }
  }, [tg_user_id]);

  //        
  useEffect(() => {
    if (tg_user_id && !isRefreshDisabled) {
      const timer = setTimeout(() => {
        handleRefresh();
      }, 300); //      
      return () => clearTimeout(timer);
    }
  }, [selectedDate, selectedReportType, handleRefresh]);

  return (
    <div className="twa-container">
      <div className="twa-page">
        <h1 className="twa-title"></h1>

        <div className="twa-header-content">
          <div className="twa-datepicker-container">
            <DatePicker
              selected={selectedDate}
              onChange={(date) => setSelectedDate(date)}
              dateFormat="yyyy-MM-dd"
              className="twa-datepicker"
              placeholderText=" "
              disabled={isRefreshDisabled}
            />
          </div>
          <div className="twa-report-type-container">
            <select
              value={selectedReportType}
              onChange={(e) => setSelectedReportType(Number(e.target.value))}
              className="twa-select"
              disabled={isRefreshDisabled}
            >
              {reportTypes.map((type) => (
                <option key={type.value} value={type.value}>
                  {type.label}
                </option>
              ))}
            </select>
          </div>
          <div className="twa-refresh-button-container">
            <Button
              name="refresh"
              title=" "
              variant="secondary"
              enabled={!isRefreshDisabled}
              onClick={handleRefresh}
            />
            <Button
              name="export"
              title=" "
              variant="secondary"
              enabled={
                Array.isArray(productsData) &&
                productsData.length > 0 &&
                !isRefreshDisabled
              }
              dropdownItems={exportDropdownItems}
              onDropdownItemClick={handleDropdownItemClick}
            />
          </div>
        </div>

        <div className="twa-summary-container">
          <div className="twa-summary-item">
            <span className="twa-summary-label">-:</span>
            <span className="twa-summary-value">{totalCount}</span>
          </div>
          <div className="twa-summary-item">
            <span className="twa-summary-label">:</span>
            <span className="twa-summary-value total">
              {totalSum.toString().replace(/\B(?=(\d{3})+(?!\d))/g, " ")} 
            </span>
          </div>
        </div>

        <div className="twa-content">
          <Table
            columns={tableColumns}
            data={productsData}
            onRowClick={handleRowClick}
            isLoading={isLoading}
          />
        </div>

        <div className="twa-footer-debug"></div>
      </div>
    </div>
  );
};

export default ProductsForm;

