import { useCallback, useEffect, useState, useRef } from "react";
import { copyToClipboard, showMessage, handleError } from "../utils/utils";

const SERVER_PORT =
  new URLSearchParams(window.location.search).get("port") || 5000;

const BOT_USERNAME =
  new URLSearchParams(window.location.search).get("bot_username") ||
  "ssv_test_bot";

const USER_ID = new URLSearchParams(window.location.search).get("user_id") || 0;

const CHAT_ID = new URLSearchParams(window.location.search).get("chat_id") || 0;

const isDevMode = () => {
  return BOT_USERNAME.toLowerCase().trim() !== "ai_sky_net_bot";
};

export const getApiUrl = () => {
  return isDevMode()
    ? `http://localhost:${SERVER_PORT}/api/dev/srv`
    : `http://195.2.75.212:${SERVER_PORT}/api/prod/srv`;
};

const MAX_RETRIES = 3;
const TIMEOUT = 10000; // 10 
const CACHE_DURATION = 5 * 60 * 1000; // 5 

const requestCache = new Map();

const getCacheKey = (url, data) => {
  return `${url}-${JSON.stringify(data)}`;
};

const getCachedResponse = (cacheKey) => {
  const cached = requestCache.get(cacheKey);
  if (cached && Date.now() - cached.timestamp < CACHE_DURATION) {
    return cached.data;
  }
  requestCache.delete(cacheKey);
  return null;
};

const setCachedResponse = (cacheKey, data) => {
  requestCache.set(cacheKey, {
    data,
    timestamp: Date.now(),
  });
};

/**
 * ,     Telegram
 * @returns {boolean} true   Telegram, false   
 */
export const isTelegramEnvironment = () => {
  //   Telegram WebApp
  const hasWebApp = !!window?.Telegram?.WebApp;

  if (!hasWebApp) {
    return false;
  }

  //     Telegram
  const isInTelegram =
    //  URL  Telegram
    window.location.search.includes("tgWebApp") ||
    //  user agent (  )
    navigator.userAgent.includes("Telegram") ||
    // ,  WebApp  ()
    !!window.Telegram?.WebApp?.initDataUnsafe?.user;

  return isInTelegram;
};

/**
 *      
 * @param {string} message -   
 * @param {string} type -   ('success', 'error', 'info')
 */
export const showNotification = (message, type = "info") => {
  try {
    // ,    Telegram  WebApp 
    if (isTelegramEnvironment() && window?.Telegram?.WebApp?.showAlert) {
      window.Telegram.WebApp.showAlert(message);
    } else {
      //    
      const emoji = type === "success" ? "" : type === "error" ? "" : "";
      alert(`${emoji} ${message}`);
    }
  } catch (error) {
    // Fallback   alert  -   
    console.warn("   :", error);
    alert(message);
  }
};

/**
 *     Telegram WebApp
 * @returns {Object}      Telegram WebApp
 * @property {string} BOT_USERNAME -  
 * @property {Object} user -  
 * @property {Object} WebApp -  Telegram WebApp
 * @property {Object} MainButton -   
 * @property {Function} onClose -   
 * @property {Function} onToggleButton -     
 * @property {Function} sendDataToServer -     
 */
export const useTelegram = () => {
  const WebApp = window?.Telegram?.WebApp;
  const MainButton = window?.Telegram?.WebApp?.MainButton;
  const user = WebApp?.initDataUnsafe?.user || null;
  const API_BASE_URL = getApiUrl();
  const abortControllerRef = useRef(null);

  /**
   *  Telegram WebApp
   */
  const onClose = useCallback(() => {
    if (!WebApp) return;
    WebApp.close();
  }, [WebApp]);

  /**
   *    
   */
  const toggleMainButton = useCallback(() => {
    if (!MainButton) return;
    if (MainButton.isVisible) {
      MainButton.hide();
    } else {
      MainButton.show();
    }
  }, [MainButton]);

  /**
   *          CSRF 
   * @param {Object} data -   
   * @returns {Promise<Object>}  
   * @throws {Error}    
   */
  const sendDataToServer = useCallback(
    async (data) => {
      if (!WebApp) return;
      if (!data) {
        showNotification("   ", "error");
        return;
      }

      const url = `${API_BASE_URL}/car/carlist/add/`;
      const cacheKey = getCacheKey(url, data);
      const cachedResponse = getCachedResponse(cacheKey);

      if (cachedResponse) {
        showNotification("   ( )", "success");
        return cachedResponse;
      }

      if (abortControllerRef.current) {
        abortControllerRef.current.abort();
      }

      abortControllerRef.current = new AbortController();
      const timeoutId = setTimeout(
        () => abortControllerRef.current.abort(),
        TIMEOUT
      );

      try {
        const send_data = {
          ...data,
          user_id: user.id,
        };

        let retries = 0;
        let lastError = null;

        while (retries < MAX_RETRIES) {
          try {
            const response = await fetch(url, {
              method: "POST",
              mode: "cors",
              headers: {
                "Content-Type": "application/json",
              },
              body: JSON.stringify(send_data),
              signal: abortControllerRef.current.signal,
            });

            if (!response.ok) {
              throw new Error(
                `HTTP ! : ${response.status}\nURL:${url}`
              );
            }

            const result = await response.json();
            setCachedResponse(cacheKey, result);
            showNotification("  ", "success");
            return result;
          } catch (error) {
            lastError = error;
            handleError(error, WebApp);

            if (error.name === "AbortError") {
              throw error;
            }

            retries++;
            if (retries < MAX_RETRIES) {
              await new Promise((resolve) =>
                setTimeout(resolve, Math.pow(2, retries) * 1000)
              );
            }
          }
        }

        throw lastError;
      } catch (error) {
        handleError(error, WebApp);
        throw error;
      } finally {
        clearTimeout(timeoutId);
      }
    },
    [WebApp, user, API_BASE_URL]
  );

  useEffect(() => {
    return () => {
      if (abortControllerRef.current) {
        abortControllerRef.current.abort();
      }
    };
  }, []);

  return {
    user,
    WebApp,
    MainButton,

    USER_ID,
    CHAT_ID,
    BOT_USERNAME,
    SERVER_PORT,
    API_BASE_URL,

    isDevMode,
    onClose,
    toggleMainButton,
    sendDataToServer,
    isTelegramEnvironment,
    showNotification,
  };
};

//  CSS- Telegram WebApp  :root
export function applyTelegramTheme(themeParams) {
  if (!themeParams || typeof window === "undefined") return;
  const root = document.documentElement;
  Object.entries(themeParams).forEach(([key, value]) => {
    if (key && value) {
      root.style.setProperty(`--tg-theme-${key.replace(/_/g, "-")}`, value);
    }
  });
}

//      Telegram
export function useApplyTelegramTheme() {
  useEffect(() => {
    if (window?.Telegram?.WebApp?.themeParams) {
      window?.Telegram?.WebApp?.ready();
      applyTelegramTheme(window.Telegram.WebApp.colorScheme);
      //const themeParams = Telegram.WebApp.themeParams;
      //const colorScheme = Telegram.WebApp.colorScheme;
    }
  }, []);
}
